<template>
    <div ref="parent">
        <FullCalendar :options="calendarOptions"/>
    </div>
</template>

<script>
import '@fullcalendar/core/vdom' // solves problem with Vite
import FullCalendar from '@fullcalendar/vue'
import dayGridPlugin from '@fullcalendar/daygrid'
import interactionPlugin from '@fullcalendar/interaction'

export default {
    name: 'Pointageagents',
    components: {
        FullCalendar
    },
    props: [
        'modellistingsData',
    ],
    data() {
        return {
            calendarOptions: {
                plugins: [dayGridPlugin, interactionPlugin],
                dateClick: this.handleDateClick,
                initialView: 'dayGridMonth',
                events: [],
                locale: "fr",
                selectable: true,
            },
            selectedDates: [] // Tableau pour stocker les dates slectionnes
        }
    },
    mounted() {
        this.generateEvents();

    },
    methods: {

        handleDateClick(info) {
            const clickedDate = info.dateStr;


            // Vrifier si la date est dj slectionne
            const index = this.selectedDates.indexOf(clickedDate);
            if (index > -1) {
                // Si la date est dj slectionne, la supprimer du tableau
                this.selectedDates.splice(index, 1);
            } else {
                // Si la date n'est pas dj slectionne, l'ajouter au tableau
                this.selectedDates.push(clickedDate);
            }

            // Mettre  jour les vnements affichs dans le calendrier
            this.updateCalendarEvents();
            console.log('selectedDates', this.selectedDates)
            this.$emit("selectDays", this.selectedDates)
        },
        updateCalendarEvents() {
            // Rinitialiser les vnements du calendrier
            this.calendarOptions.events = [];

            // Parcourir toutes les dates slectionnes et ajouter des vnements correspondants
            this.selectedDates.forEach(date => {
                this.calendarOptions.events.push({
                    title: 'Selectionner',
                    date: date
                });
            });
        },

        generateEvents() {

            const modellistingsDates = this.modellistingsData.date;
            const obj = JSON.parse(modellistingsDates);
            console.log('modellistingsDates', obj)
            this.selectedDates = obj;
            // const progammeDates = this.modellistingsData.Pointage2;
            const events = [];
            // Ajouter les vnements  partir de modellistingsDates
            obj.forEach(date => {
                events.push({
                    title: 'Selectionner',
                    date: date.split(' ')[0]
                });
            });


            this.calendarOptions.events = events;
        },
    }
}
</script>


